import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { LoginComponentModel } from '../utils';
import { AuthService } from '@app/shared';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html'
})
export class LoginComponent implements OnInit {

  constructor(private service: AuthService) { }

  model = new LoginComponentModel();
  disable = false;

  login() {
    this.service.login(this.model.phone, this.model.password).subscribe(_ => {
      window.location.href = window.location.origin + ''
    });
    console.log(this.model);
  }
  public ngOnInit() {
    $(document).ready(function () {
      $('.toggle-password').click(function () {
        $(this).toggleClass('fa-eye fa-eye-slash');
        const toggle = $(this).attr('toggle');
        const stateInput = $('.' + toggle).attr('type');
        if (stateInput === 'password') {
          $('.' + toggle).attr('type', 'text');
        } else {
          $('.' + toggle).attr('type', 'password');
        }
      });
    });
  }
}
